@import "https://fonts.googleapis.com/css2?family=DM+Sans:opsz,wght@9..40,500&family=Inter:wght@400;500;600&family=Poppins:wght@600&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

:root {
  --light-color: #fff;
  --dark-color: #050505;
  --accent-color: #9bb537;
  --stars-color: #eea10c;
  --btn-text-color: #f8f8f8;
  --light-theme-text-color: #050505;
  --light-theme-semi-transparent-color: rgba(5, 5, 5, .5);
  --light-theme-almost-transparent-color: rgba(5, 5, 5, .3);
  --light-theme-transparent-color: rgba(5, 5, 5, .2);
  --light-theme-slider-semi-transparent-color: rgba(248, 248, 248, .5);
  --dark-theme-background-color: #1e1e1e;
  --dark-theme-text-color: #fff;
  --dark-theme-semi-transparent-color: rgba(255, 255, 255, .5);
  --dark-theme-almost-transparent-color: rgba(255, 255, 255, .3);
  --dark-theme-transparent: rgba(255, 255, 255, .2);
  --animation: 250ms cubic-bezier(.23, .86, .79, .77);
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: Inter, sans-serif;
}

.is-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.no-scroll {
  overflow: hidden;
}

.rad-img {
  border-radius: 15px;
  overflow: hidden;
}

.section {
  padding: 0 15px;
}

.container {
  outline: 2px solid #00f;
  margin: 0 auto;
}

@media screen and (min-width: 375px) {
  .section {
    padding: 0 20px;
  }

  .container {
    max-width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .section {
    padding: 0 32px;
  }

  .container {
    max-width: 768px;
  }

  .sections-flex {
    gap: 32px;
    display: flex;
  }
}

@media screen and (min-width: 1200px) {
  .section {
    padding: 0 100px;
  }

  .container {
    max-width: 1080px;
  }

  .sections-flex {
    gap: 63px;
  }
}

.base-btn {
  color: var(--btn-text-color);
  background-color: var(--accent-color);
  border: 1px var(--accent-color) solid;
  transition: color var(--animation);
  border-radius: 15px;
  padding: 14px 28px;
}

.base-btn:hover, .base-btn:focus {
  color: var(--dark-color);
}

.error-el > p {
  text-align: center;
}

.header-container {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 85px;
  display: flex;
}

.header-list {
  display: none;
}

.header-logo {
  color: var(--accent-color);
  text-align: center;
  letter-spacing: -.64px;
  margin-top: 30px;
  margin-left: 20px;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.125;
}

.part-logo {
  color: var(--dark-color);
}

.header-buttons {
  align-items: center;
  gap: 12px;
  margin-right: 20px;
  display: flex;
}

.header-basket {
  width: 32px;
  height: 32px;
  background-color: var(--light-color);
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
}

.header-switch {
  display: none;
}

.header-burger {
  width: 32px;
  height: 32px;
  background-color: var(--light-color);
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
}

.switch {
  display: none;
}

.menu {
  width: 50vw;
  height: 100vh;
  z-index: 2;
  background-color: var(--accent-color);
  transition: opacity var(--animation), visibility var(--animation), left var(--animation);
  flex-direction: column;
  justify-content: space-between;
  padding: 20px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.menu.is-hidden {
  left: -100%;
}

.menu-close {
  width: 32px;
  height: 32px;
  background: var(--accent-color);
  border: 1px solid #2e2f42;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  display: flex;
}

.menu-block {
  margin-left: auto;
  margin-right: auto;
}

.icon-mnu {
  fill: var(--light-color);
}

.menu-item:not(:last-child) {
  margin-bottom: 16px;
}

.menu-link {
  color: #fff;
  text-align: center;
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  display: block;
}

.menu-switch {
  width: 46px;
  height: 21px;
  display: inline-block;
  position: relative;
}

.menu-switch input {
  display: none;
}

.menu-slider {
  cursor: pointer;
  background-color: #cecdcd;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.menu-slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background-color: var(--light-color);
  transition: all .4s;
  position: absolute;
  bottom: 2px;
  left: 4px;
}

input:checked + .menu-slider {
  background-color: var(--accent-color);
}

input:focus + .menu-slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .menu-slider:before {
  transform: translateX(18px);
}

.menu-slider.menu-round {
  border-radius: 20px;
}

.menu-slider.menu-round:before {
  border-radius: 60%;
}

@media screen and (min-width: 768px) {
  .header-list {
    align-items: center;
    gap: 16px;
    margin-left: 30px;
    display: flex;
  }

  .header-item {
    color: var(--dark-color);
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  .header-navigation {
    display: flex;
  }

  .header-logo {
    letter-spacing: -.72px;
    margin-top: auto;
    padding-left: 200px;
    font-size: 18px;
    line-height: 1.111;
  }

  .header-burger {
    display: none;
  }

  .switch {
    width: 46px;
    height: 21px;
    display: inline-block;
    position: relative;
  }

  .switch input {
    display: none;
  }

  .slider {
    cursor: pointer;
    background-color: #cecdcd;
    transition: all .4s;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .slider:before {
    content: "";
    height: 18px;
    width: 18px;
    background-color: var(--light-color);
    transition: all .4s;
    position: absolute;
    bottom: 2px;
    left: 4px;
  }

  input:checked + .slider {
    background-color: var(--accent-color);
  }

  input:focus + .slider {
    box-shadow: 0 0 1px #2196f3;
  }

  input:checked + .slider:before {
    transform: translateX(18px);
  }

  .slider.round {
    border-radius: 20px;
  }

  .slider.round:before {
    border-radius: 60%;
  }

  .header-buttons {
    margin-right: 30px;
  }
}

@media screen and (min-width: 1200px) {
  .header-buttons {
    margin-right: 100px;
  }

  .header-navigation {
    margin-left: 100px;
  }

  .header-list {
    margin-left: 0;
  }

  .header-container {
    padding-bottom: 100px;
  }
}



.categories-section {
  padding: 40px 8px 20px 0;
}

.categories-btn {
  background-color: inherit;
  border: 1px var(--light-theme-transparent-color) solid;
  color: var(--light-theme-semi-transparent-color);
  transition: color var(--animation), background-color var(--animation), border var(--animation);
  margin: 0 0 24px;
  font-size: 18px;
  font-weight: 500;
}

.categories-btn:hover, .categories-btn:focus {
  color: var(--btn-text-color);
  background-color: var(--accent-color);
  border: 1px var(--accent-color) solid;
}

.categories-container {
  width: 100%;
  height: 212px;
  overflow: auto;
}

.category {
  margin-bottom: 18px;
}

.category-btn {
  background-color: inherit;
  color: var(--light-theme-almost-transparent-color);
  transition: color var(--animation);
  border: rgba(0, 0, 0, 0);
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28;
}

.category-btn:hover, .category-btn:focus, .category-btn-active {
