}

.categories-container::-webkit-scrollbar-thumb {
  width: 8px;
  height: 116px;
  background-color: rgba(5, 5, 5, .18);
  border: rgba(0, 0, 0, 0);
  border-radius: 12px;
}

@media screen and (min-width: 375px) {
  .categories-container {
    width: 326px;
  }
}

@media screen and (min-width: 768px) {
  .categories-section {
    padding: 53px 0 32px;
  }

  .categories-btn {
    margin: 0 0 30px;
  }

  .categories-container {
    width: 176px;
    height: 416px;
  }

  .category {
    margin-bottom: 24px;
  }

  .category-btn {
    font-size: 16px;
    line-height: 1.25;
  }

  .categories-container::-webkit-scrollbar-thumb {
    height: 201px;
  }
}

@media screen and (min-width: 1200px) {
  .categories-section {
    padding: 63px 0 32px;
  }

  .categories-btn {
