.btn-up {
  cursor: pointer;
  color: #fff;
  width: 50px;
  height: 50px;
  opacity: 1;
  background-color: #6af500;
  border-radius: 25px;
  justify-content: center;
  align-items: center;
  transition: opacity .15s ease-in-out, transform .3s ease-in-out;
  display: flex;
  position: fixed;
  bottom: 30px;
  left: 20px;
  transform: translateY(0);
}

.btn-up_hide {
  display: none;
}

.btn-up_hiding {
  opacity: 0;
  transform: translateY(100px);
}

.btn-up-icon {
  width: 40px;
  height: 40px;
  fill: currentColor;
}

@media (hover: hover) and (pointer: fine) {
  .btn-up:hover {
    background-color: #00bfef;
  }
}

.mask {
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  transition: all .6s;
  display: flex;
  position: fixed;
  top: 0;
}

.loader {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  animation: rotate 1s linear infinite;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  box-sizing: border-box;
  border: 5px solid #9bb537;
  border-radius: 50%;
  animation: prixClipFix 2s linear infinite;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.loader:after {
  border-color: #ebe6db;
  animation: prixClipFix 2s linear infinite, rotate .5s linear infinite reverse;
  top: 6px;
  bottom: 6px;
