.filter {
    background-color: var(--btn-text-color);
}

.filter-flex-wrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 14px;
    margin-bottom: 14px;
}

.filter-name {
    display: block;
    font-size: 14px;
    line-height: 1.29;
    letter-spacing: 0em;
    margin-bottom: 8px;
    color: var(--light-theme-semi-transparent-color);
}

.filter-input-wrapper {
    display: block;
    position: relative;
}

.search-input {
    /* width: 335px; */
    height: 46px;
    border: 1px solid var(--light-theme-transparent-color);
    border-radius: 15px;
    padding-left: 48px;
    background-color: var(--btn-text-color);
}

.search-input:focus {
    outline: none;
}

.filter-input-icon {
    position: absolute;
    top: 50%;
    left: 18px;
    transform: translateY(-50%);
    fill: var(--light-theme-semi-transparent-color);
    transition: fill var(--animation);
}

.search-input:focus + .filter-input-icon {
    fill: var(--accent-color);
}

.time-select {
    width: 160px !important;
    height: 46px;
    outline: none !important;
}

.area-select {
    width: 161px !important;
    height: 46px;
    outline: none !important;
}

.ingredients-select {
    width: 188px !important;
    height: 46px;
    outline: none !important;
}




.filter-reset {
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    width: 105px;
    height: 16px;
    padding: 0 0;
    margin-left: auto;
    background-color: var(--btn-text-color);
}

.filter-reset:focus {
    outline: none;
}

.filter-reset-desc {
    display: block;
    font-size: 12px;
    line-height: 1.33;
    letter-spacing: 0em;
    color: var(--light-theme-semi-transparent-color);
}

.filter-reset-icon {
    margin-right: 8px;
    width: 8px;
    height: 8px;
    fill: var(--accent-color);
}

/* ------   slim-select------- */

:root{
    --ss-content-height: 176px !important;
    --ss-border-radius: 15px !important;
}

.ss-main .ss-values .ss-single{
    margin: auto 0px auto 18px !important;
}

.ss-main .ss-arrow{
    margin: auto 18px !important;
}

.ss-content .ss-list .ss-option{
    padding-left: 18px !important;
}

.ss-list::-webkit-scrollbar {
    width: 14px !important;
    margin-right: 4px !important;
}

.ss-main {
    background-color: var(--btn-text-color) !important;
}

.ss-main:focus {
    box-shadow: none !important
}

/* ------------------- */

.ss-list::-webkit-scrollbar-thumb {
    width: 6px;
    background-color: rgb(5, 5, 5, 0.1);
    border-radius: 12px;
    border: 4px solid var(--light-color);
}

.ss-list::-webkit-scrollbar-thumb:hover {
    background-color: var(--light-theme-transparent-color);
}



@media screen and (max-width: 366px) {
.search-input {
    width: 290px;
}
}

@media screen and (min-width: 367px) {
.search-input {
    width: 335px;
}
}

@media screen and (min-width: 768px) {

.filter-form {
    gap: 16px;
}
}

@media screen and (min-width: 1200px) {

}







